<template>
  <div>
    <v-parallax src="@/assets/pexels-brett-sayles-2881233.jpg" height="300"></v-parallax>
    <v-container mt-16>
      <div class="text-center">
        <p class="display-2 font-weight-thin">P.Q.R.S</p>
        <p>Peticiones, quejas, reclamos, sugerencias</p>
      </div>
      <v-row justify="center">
        <v-col cols="12" md="5">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3984.5537079723254!2d-75.28954798474756!3d2.9436897552010044!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x8e3b745bed41a855%3A0xc3c79701ee3df032!2sCra.%208c%20%2326-54%2C%20Neiva%2C%20Huila!5e0!3m2!1sen!2sco!4v1596488270370!5m2!1sen!2sco"
            width="100%"
            height="100%"
            frameborder="0"
            allowfullscreen="allowfullscreen"
            aria-hidden="false"
            tabindex="0"
            style="border: 0px;"
          ></iframe>
        </v-col>
        <v-col cols="12" md="7">
          <v-container>
            <v-alert
              type="success"
              elevation="12"
              border="top"
              dismissible
            >Tu Queja fue enviada con exito.</v-alert>
            <v-alert type="error" elevation="12" border="bottom" dismissible>Algo salio mal.</v-alert>
            <v-form v-model="valid" ref="form">
              <v-row>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="txtEmailQueja"
                    :rules="txtEmailQuejaRules"
                    label="E-mail"
                    required
                    outlined
                    clearable
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="txtNombreQueja"
                    :rules="txtNombreQuejaReglas"
                    label="Nombre"
                    required
                    outlined
                    clearable
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="numTelefonoQueja"
                    :rules="numTelefonoQuejaReglas"
                    label="Telefono"
                    required
                    outlined
                    clearable
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="txtciudadQueja"
                    :rules="txtciudadQuejaReglas"
                    label="Ciudad"
                    required
                    outlined
                    clearable
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    outlined
                    v-model="txtMensajeQueja"
                    :rules="txtMensajeQuejaReglas"
                    label="Mensaje"
                    required
                    clearable
                  ></v-textarea>
                </v-col>
                <v-col cols="12">
                  <v-file-input
                    label="File input"
                    outlined
                    dense
                    hint="Sí tine un documento de soporte por favor adjuntarlo. (PDF, JPG, PNG, WORD)"
                    persistent-hint
                  ></v-file-input>
                </v-col>
              </v-row>

              <v-btn rounded color="primary" dark>Enviar</v-btn>
              <v-btn rounded color="error" dark @click="reset">Cancelar</v-btn>
            </v-form>
          </v-container>
        </v-col>
      </v-row>
    </v-container>
    <v-parallax src="@/assets/StockSnap_FBXB2DA8O7.jpg" height="300"></v-parallax>
  </div>
