<script>
export default {
  data: () => ({
    valid: false,
    txtNombreQueja: "",
    txtNombreQuejaReglas: [
      (v) => !!v || "Nombre es requerido",
      //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    ],
    numTelefonoQueja: "",
    numTelefonoQuejaReglas: [
      (v) => !!v || "Telefono es requerido",
      //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    ],
    txtciudadQueja: "",
    txtciudadQuejaReglas: [
      (v) => !!v || "Telefono es requerido",
      //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    ],
    txtMensajeQueja: "",
    txtMensajeQuejaReglas: [
      (v) => !!v || "Mensaje es requerido",
      //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    ],
    txtMensajeQueja: "",
    txtMensajeQuejaReglas: [
      (v) => !!v || "Mensaje es requerido",
      //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    ],
    txtEmailQueja: "",
    txtEmailQuejaRules: [
      (v) => !!v || "E-mail es requerido",
      (v) => /.+@.+/.test(v) || "E-mail debe ser valido",
    ],
  }),
  methods: {
    reset() {
      this.$refs.form.reset();
    },
  },
};
</script>
